.widget-panel, 
.content-panel { /*modules*/ /*components*/ /*_panels.scss*/
  	position: relative;
  	z-index: 0;
  	border: 1px solid $border;
}

.app-guide {
  	position: relative;
  	z-index: 0;
}

.app-guide-panel {
 	.modal-dialog {
		width: 750px;
	}
}

.widget-panel .widget-panel__body, .app-guide .app-guide__body { padding: 3rem 3rem ; } 
.widget-panel .widget-panel__head, .app-guide .app-guide__head  { padding: 1.5rem 3rem ;}

.content-panel .content-panel__body { padding: 2rem 3rem; }
.content-panel .content-panel__head { padding: 2rem 3rem; } 

.widget-panel .widget-panel__head { min-height: 59px; } 
.widget-row-top .widget-panel { min-height: 300px; } 
.widget-row-middle .widget-panel { min-height: 275px; } 
.widget-panel--max { max-height: 450px; overflow-y: auto; overflow-x: hidden; }